.createPostPage {
    display: flex;
    flex-direction: column;
    align-items: center;
    min-height: 100vh;
    background-color: #F1E1C8;
    padding: 20px;
    // font-family: 'Arial', sans-serif;
    max-width: 1000px; // 최대 너비를 1000px로 조정
    margin: 0 auto;
}

.headerNav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    max-width: 700px; // 폼의 최대 너비와 일치
    margin-bottom: 20px;
    padding: 0; // 패딩 제거
}

.backButton,
.homeButton {
    font-size: 24px; // 버튼 크기 약간 축소
    color: #333;
    background: none;
    border: none;
    cursor: pointer;
    padding: 5px; // 패딩 축소
    transition: transform 0.3s ease;

    &:hover {
        transform: scale(1.1);
    }
}

.postTitle {
    font-size: 28px; // 제목 크기 약간 축소
    font-weight: bold;
    margin-bottom: 20px;
    color: #333;
    text-align: center;
    cursor: pointer;
}

.form {
    width: 100%;
    max-width: 700px; // 폼의 최대 너비 축소
    background-color: #fff;
    padding: 25px; // 내부 패딩 약간 축소
    border-radius: 12px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.input,
.textarea {
    width: 100%;
    padding: 12px; // 입력 필드 패딩 약간 축소
    margin-bottom: 15px;
    border: 1px solid #ddd;
    border-radius: 8px;
    font-size: 15px; // 폰트 크기 약간 축소
    transition: border-color 0.3s;
    box-sizing: border-box;

    border: 3px solid #cfbbbb;
        background: none;
        padding: 1rem;
        font-size: 1rem;
}

.textarea {
    min-height: 180px; // 텍스트 영역 높이 약간 축소
}

.imageLabel {
    display: inline-block;
    padding: 10px 18px; // 패딩 약간 축소
    background-color: #f0f0f0;
    color: #333;
    border-radius: 8px;
    cursor: pointer;
    transition: background-color 0.3s;
    font-size: 15px; // 폰트 크기 약간 축소

    &:hover {
        background-color: #e0e0e0;
    }
}

.submitButton {
    width: 100%;
    padding: 12px; // 패딩 약간 축소
    background-color: #B3797A;
    color: white;
    border: none;
    border-radius: 8px;
    font-size: 16px; // 폰트 크기 약간 축소
    font-weight: bold;
    cursor: pointer;
    transition: background-color 0.3s;

    &:hover {
        background-color: #9F5A5B;
    }
}

@media (max-width: 768px) {
    .createPostPage {
        padding: 10px;
    }

    .headerNav {
        max-width: 100%;
    }

    .postTitle {
        font-size: 22px;
    }

    .form {
        padding: 15px;
    }

    .input,
    .textarea,
    .submitButton {
        font-size: 14px;

            border: 3px solid #cfbbbb;
                background: none;
                padding: 1rem;
                font-size: 1rem;
               
    }
}

.imagePreview{
    display: flex;
    flex-wrap: wrap;/* 줄 바꿈 허용 */
    margin-top: 10px;
    margin-bottom:10px;
}
.imageItem{
    margin-right: 10px;
    margin-bottom: 10px;
        /* 아래쪽 여백 추가 */
}

.imageUpload {
    background-color: #f0f0f0; // 기본 배경색
    transition: background-color 0.3s; // 배경색 변화 애니메이션
    margin-bottom: 10px;
}

.imageUpload.hasImages {
    background-color: #8EBC8F; // 이미지가 있는 경우 변경할 색상
}

.imageLabel {
    color: gray; // 기본 색상
    color: #FFFFFF; // 글자색
}

.imageLabelActive {
     // 이미지가 있을 때의 색상
    background-color: #218838; // 배경색
        color: #FFFFFF; // 글자색
}

.imageLabelInactive {
    background-color: gray; // 이미지가 없을 때의 색상
}